
@import url('https://fonts.googleapis.com/css2?family=Golos+Text&family=Inter:wght@100;200;300;400;500;600&family=Poppins:wght@100&family=Ruda&family=Schibsted+Grotesk:ital@1&display=swap');

*{
  padding: 0;
  margin: 0;
  font-family: poppins;
  font-size: 30px;
}

.project{
  /* border: 1px solid red; */
  display: flex;
  width: 100vw;
  
}

.left-side{
  background-color:#B1D9DB;
  width: 40vw;
  color: white;
  height: 100vh;
  display: flex;
  align-items: center;
  padding-left: 40px;

}

.image{
  width: 100%;
  position: relative;
  left: 100px;
}

.right-side{
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* border: 1px solid red; */
  align-items: center;
  width: 100vw;
  height: 100vh;
}

input{
  border: none;
  border-bottom: 1px solid rgb(206, 205, 205);
  font-family: poppins;
  font-weight: 100;
  font-size: 20px;
  width: 70%;
  margin-top: 25px;
  outline: none;
}

button{
  background-color: #7CD2D7;
  margin-top: 40px;
  width: 70%;
  color: white;
  border: none;
  border-radius: 10px;
}

h5{
  color: #7CD2D7;
  margin-top: 25px;
  display: none;
  font-size: 20px;
}

#failmsg,#blankmsg{
color: red;
}